    /**
     * 根据不同平台加载不同的js文件
     */
    loadPlatformScript = function(cbFunc, params){
        //判断是否是手机微端，是则调用微端js sdk，否则调用h5 sdk
        if(util.BroswerUtil.isMiniClientExcludeCG()){
            // util.loadScripts(["https://d1wh375sy22h3n.cloudfront.net/h5/v2.1/wing_sdk.js", "https://d1wh375sy22h3n.cloudfront.net/h5/v2.1/wing_android_sdk.js", "https://d1wh375sy22h3n.cloudfront.net/h5/v2.1/wing_ios_sdk.js"], function(){
            // util.loadScripts(["js/miniclient/wing_sdk.js", "js/miniclient/wing_android_sdk.js", "js/miniclient/wing_ios_sdk.js"], function(){
            util.loadScripts([MINICLIENT_SDK_URL, MINICLIENT_SDK_ANDROID_URL, MINICLIENT_SDK_IOS_URL], function(){
            // util.loadScripts(["js/miniclient/wing_miniclient_sdk.min.js"], function(){
                console.info('Load mobile script successfully...');
                if(cbFunc){
                    console.info('Invoke function after load mobile script successfully...', cbFunc);
                    eval(cbFunc+"(params)");
                }
            })
        }else{
            // util.loadScripts(["https://d1wh375sy22h3n.cloudfront.net/h5/v2.1/wing.min.js"], function(){
            // util.loadScripts(["http://172.16.100.58/sdk_resource/h5/js/wing.min.js"], function(){
            //util.loadScripts(["http://sdk-test1.gamehollywood.com/sdk_resource_qa/h5/js/wing.min.js"], function(){
            // util.loadScripts(["js/h5sdk/wing.min.js"], function(){
            util.loadScripts([H5_SDK_URL], function(){
                console.info('Load h5 script successfully...');
                if(cbFunc){
                    console.info('Invoke function after load h5 script successfully...', cbFunc);
                    eval(cbFunc+"(params)");
                }
            })
        }
    };

    // 自动根据平台加载js脚本
    loadPlatformScript();

    checkInitAndInvokeFunc = function(cbFunc, params){
        //如果还未加载到相应平台的js，重试，n毫秒后执行回调方法
        if(typeof wing === "undefined" || !wing){
            console.warn('wing is not defined, reload script...');
            // loadPlatformScript(cbFunc);
            // setTimeout(function () {
            //   checkInitAndInvokeFunc(cbFunc, params);
            // }, 500);
            util.wait(500).then(function(){
                checkInitAndInvokeFunc(cbFunc, params);
            });
        }else{
            if(cbFunc){
                console.log('Callback function will be invoked...', cbFunc);
                eval(cbFunc+"(params)");
            }
        }
    };

    wingplus.init = function(params){
        //初始化方法只有h5sdk有
        if(!util.BroswerUtil.isMiniClientExcludeCG()){
            checkInitAndInvokeFunc("wing.init", params);
        }
    };
    util.register("wingplus.init",wingplus.init);

    /*****************************************common begin*****************************************/
    callbackFunc = function(cbFunc, params){
        if(cbFunc &&  typeof cbFunc == 'function'){
            console.log('CallbackFunc will be invoked...', cbFunc);
            cbFunc(params);
        }else{
            console.error('Error occur, callBack function is invalid...');
        }
    }
