    /**
     * 判断支付是否可用
     * callback为回调函数，函数中会返回一个布尔值，表示支付是否可用
     */
    wingplus.pay.isPayServiceAvailable = function(callback){
        if(util.isNull(callback)){
            console.warn("isPayServiceAvailable callback should not be null");
            return;
        }
        wing.pay.isPayServiceAvailable(callback);
    }

    /**
     * 获取商品列表
     *
     * @param {json object} paramObj 对象，包含以下属性
     * @param {function} success, 必选，成功回调，用来返回登录成功用户信息json字符串结果
     * @param {function} fail, 必选，失败回调，用来返回登录失败结果
     * @param {function} cancel, 必选，取消回调，用来返回登录取消结果
     * @return {json object} 返回结果存放于回调函数中，包含以下字段
     * @return {number} code 不为空，接口调用返回代码，详见对接文档代码说明
     * @return {string} msg 不为空，结果描述
     * @return {json object} productList 成功获取到商品列表时不为空，包括以下属性
     * @return {string} productId 不为空，商品ID,购买时使用
     * @return {number} productName 不为空，商品名称
     * @return {string} productDesc 可能为空，商品描述
     * @return {number} gameCurrencyAmount 可能为空，该商品对应的游戏币数量（目前iOS不可用）
     */
    wingplus.pay.getProducts = function(paramObj){

        wing.pay.isPayServiceAvailable(function(isPayAvailable){
            if(isPayAvailable){
                if(util.BroswerUtil.isMiniClientExcludeCG()){
                    wing.pay.queryProducts(paramObj);
                }else{
                    var products=  wing.pay.getProducts();
                    var productsResult = {};
        
                    if(products){
                        //成功回调
                        if(products.length > 0){
                            productsResult.code = 200;
                            productsResult.msg = "Get products successfully!";
                            productsResult.productList = products;
                            callbackFunc(paramObj.success, productsResult);
                        }else{
                            //失败回调，获取到商品成功但是结果为空也当成是失败
                            productsResult.code = 400;
                            productsResult.msg = "Get products fail!";
                            callbackFunc(paramObj.fail, productsResult);
                        }
                    }else{
                        //取消回调，一般不会执行到这里
                        productsResult.code = -100;//取消
                        productsResult.msg = "Get products cancel!";
                        callbackFunc(paramObj.cancel, productsResult);
                    }
                }
            }else{
                var productsResult = {};
                //失败回调，支付已关闭
                productsResult.code = 4026;
                productsResult.msg = "Payment is closed";
                callbackFunc(paramObj.fail, productsResult);
            }
        })
    }

    /**
     * 购买商品
     *
     * @param {json object} paramObj 对象，包含以下属性
     * @param {string} channel, 可选，目前只适用于h5sdk的FACEBOOK
     * @param {string} productId, 必选，商品Id
     * @param {function} success, 必选，成功回调，用来返回购买成功后的信息
     * @param {function} fail, 可选，失败回调，用来返回登录失败结果
     * @param {function} cancel, 可选，取消回调，用来返回登录取消结果
     * @return {json object} 返回结果存放于回调函数中，包含以下字段
     * @return {number} code 不为空，接口调用返回代码，详见对接文档代码说明
     * @return {string} msg 不为空，结果描述
     * @return {string} amount 不为空，商品ID,支付金额
     * @return {string} currency 不为空，支付币种
     * @return {number} quantity 不为空，购买数量，目前一般都是1
     * @return {string} platform 可能为空，只适用于微端，支付渠道
     * @return {string} orderId 可能为空，只适用于微端，订单Id
     * @return {string} productId 可能为空，只适用于微端，商品Id
     * @return {string} payment_id 可能为空，只适用于h5端的FACEBOOK支付渠道
     * @return {string} request_id 可能为空，只适用于h5端的FACEBOOK支付渠道
     * @return {string} signed_request 可能为空，只适用于h5端的FACEBOOK支付渠道
     * @return {string} status 可能为空，只适用于h5端的FACEBOOK支付渠道
     */
    wingplus.pay.pay = function(paramObj){
        wing.pay.isPayServiceAvailable(function(isPayAvailable){
            if(isPayAvailable){
                if(util.BroswerUtil.isMiniClientExcludeCG()){
                    wing.pay.pay(paramObj);
                }else{
                    wing.pay.payUI(paramObj);
                }
            }else{
                var productsResult = {};
                //失败回调，支付已关闭
                productsResult.code = 4026;
                productsResult.msg = "Payment is closed";
                callbackFunc(paramObj.fail, productsResult);
            }
        })
    }
