    util.BroswerUtil = {
        //检测浏览器版本
        getBrowserVersion: function () {
            var agent = navigator.userAgent.toLowerCase();
            var arr = [];
            var Browser = "";
            var Bversion = "";
            var verinNum = "";
            //IE
            if (agent.indexOf("msie") > 0) {
                var regStr_ie = /msie [\d.]+;/gi;
                Browser = "IE";
                Bversion = "" + agent.match(regStr_ie)
            }
            //firefox
            else if (agent.indexOf("firefox") > 0) {
                var regStr_ff = /firefox\/[\d.]+/gi;
                Browser = "firefox";
                Bversion = "" + agent.match(regStr_ff);
            }
            //Chrome
            else if (agent.indexOf("chrome") > 0) {
                var regStr_chrome = /chrome\/[\d.]+/gi;
                Browser = "chrome";
                Bversion = "" + agent.match(regStr_chrome);
            }
            //Safari
            else if (agent.indexOf("safari") > 0 && agent.indexOf("chrome") < 0) {
                var regStr_saf = /version\/[\d.]+/gi;
                Browser = "safari";
                Bversion = "" + agent.match(regStr_saf);
            }
            //Opera
            else if (agent.indexOf("opera") >= 0) {
                var regStr_opera = /version\/[\d.]+/gi;
                Browser = "opera";
                Bversion = "" + agent.match(regStr_opera);
            } else {
                var browser = navigator.appName;
                if (browser == "Netscape") {
                    var version = agent.split(";");
                    var trim_Version = version[7].replace(/[ ]/g, "");
                    var rvStr = trim_Version.match(/[\d\.]/g).toString();
                    var rv = rvStr.replace(/[,]/g, "");
                    Bversion = rv;
                    Browser = "IE"
                }
            }
            verinNum = (Bversion + "").replace(/[^0-9.]/ig, "");
            arr.push(Browser);
            arr.push(verinNum);
            return arr;
        },
        //检测当前操作系统
        CurrentSystem: (function () {
            var system = {};

            var ua = navigator.userAgent;
            // 检测平台
            var p = navigator.platform;
            if (p.indexOf('Win') == 0) {
                system.windows = p.indexOf('Win') == 0;
            }
            if (p.indexOf('Mac') == 0) {
                system.mac = p.indexOf('Mac') == 0;
            }
            if (p.indexOf('Xll') == 0 || p.indexOf('Linux') == 0) {
                system.linux = (p.indexOf('Xll') == 0 || p.indexOf('Linux') == 0);
            }
            // 检测Windows操作系统
            if (system.windows) {
                if (/Win(?:dows )?([^do]{2})\s?(\d+\.\d+)?/.test(ua)) {
                    // system.windows = RegExp['$1'] + " " + RegExp['$2']
                    system.windows = RegExp['$2']
                }
            }
            // 移动设备
            if (ua.indexOf('iPhone') > -1) {
                system.ios = ua.indexOf('iPhone') > -1;
            }
            if (ua.indexOf('iPod') > -1) {
                system.ios = ua.indexOf('iPod') > -1;
            }
            if (ua.indexOf('iPad') > -1) {
                system.ios = ua.indexOf('iPad') > -1;
            }
            if (ua.indexOf('nokiaN') > -1) {
                system.nokiaN = ua.indexOf('nokiaN') > -1;
            }
            // 检测IOS版本
            if (system.ios && ua.indexOf('Mobile') > -1) {
                if (/CPU (?:iPhone )?OS ((\d+_)*\d+)/i.test(ua)) {
                    system.ios = (RegExp['$1'].replace(/_/g, '.'));
                } else {
                    system.ios = "unknown";        // 不能真正检测出来，所以只能猜测
                }
            }
            // 检测Android版本
            if (/Android ((\d+\.)*\d+)/i.test(ua)) {
                system.android = (RegExp['$1']);
            }
            // 游戏系统
            if (ua.indexOf('Wii') > -1) {
                system.wii = ua.indexOf('Wii') > -1;
            }
            if (/PlayStation/i.test(ua)) {
                system.ps = /PlayStation/i.test(ua);
            }
            return system
        })(),

        getSystem:function(){
            var systemInfo = this.CurrentSystem;
            if(!systemInfo){
                return null;
            }
            var currSystem;
            for (var k in systemInfo){
                currSystem = k;
            }
            return currSystem;
        }
        ,
        isMobileDevice : function(){
            var systemInfo = this.CurrentSystem;
            if(!systemInfo){
                return false;
            }
            var currSystem;
            for (var k in systemInfo){
                currSystem = k;
            }
            if(util.inGhwMobileArray(currSystem)){
                return true;
            }
            return false;
        },

        isCG: function(){
            if(define.context && define.context.h5Platform == true){
                return true;
            }else if(util.getUrlParams('sdkToken')){
                if(define.context){
                    define.context.h5Platform=true;
                }
                return true;
            }else{
                return false;
            }
        },

        isMiniClient : function(){
            if(this.isMobileDevice() 
                && navigator.userAgent.indexOf('wingMiniClient') > -1){
                return true
            }else{
                return false
            }
        },

        /**
         * 是否是排除了CG微端之后的微端
         */
        isMiniClientExcludeCG : function(){
            if(this.isMobileDevice() 
                && navigator.userAgent.indexOf('wingMiniClient') > -1
                && !this.isCG()){
                return true
            }else{
                return false
            }
        },

        /**
         * 是否是CG微端
         */
        isCGMiniClient : function(){
            if(this.isMobileDevice() 
                && navigator.userAgent.indexOf('wingMiniClient') > -1
                && this.isCG()){
                return true
            }else{
                return false
            }
        }
    };

    util.loadScripts = function(array,callback){
        var loader = function(src,handler){

            var script = document.createElement("script");
            script.src = src;
            script.onload = script.onreadystatechange = function(){
                script.onreadystatechange = script.onload = null;
                handler();
            }
            var head = document.getElementsByTagName("head")[0];
            (head || document.body).appendChild( script );
        };
        (function run(){
            if(array.length!=0){
                loader(array.shift(), run);
            }else{
                callback && callback();
            }
        })();
    };

