    function receipt (functionName,value){
        if(functionName &&  typeof functionName == 'function'){
            functionName(value);
        }else{
            util.log("回调方法  无法调用")
        }
    }
    function isLogin(){
        if(define.context.userLogin){
            return true;
        }
        return false;
    }

    util.register("wingplus.bind",bind);
